@mixin onTablet {
  @media (min-width: 744px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1260px) {
    @content;
  }
}

@mixin burger-size($size) {
  height: $size;
  width: $size;
}

@mixin phone-size($size) {
  height: $size;
  width: $size;
}

@mixin top-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
